.contact_form {
  padding: 100px 0 100px 0;
}
.contact_form h1,
.firstP {
  text-align: center;
}
.contact_form h1 {
  color: #000;
  margin-bottom: 30px;
}
.contact_wrapper {
  width: 753px;
  margin: 0 auto;
  color: #636363;
}
form p {
  text-align: left;
}
.red {
  color: red;
}
.form_inputs {
  margin-top: 100px;
  display: flex;
}
textarea {
  width: 100%;
  height: 163px;
}
input {
  width: 360px;
  height: 40px;
  font-size: 20px;
  margin-bottom: 30px;
}
input:first-of-type {
  margin-right: 23px;
}
input,
textarea {
  border: 1px solid #999999;
  margin-top: 5px;
}
.button_block {
  display: flex;
  justify-content: flex-end;
}
input[type="submit"] {
  width: 165px;
  height: 40px;
  background: #fcac45;
  color: #fff;
  margin-right: 0;
  border: none;
  margin-top: 20px;
  opacity: 0.7;
}
.button_block input:hover {
  opacity: inherit;
}
